}
.pure-list__input {
  position: relative;
}
.pure-list__input input {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  opacity: 0;
  cursor: pointer;
}
.pure-list__input input[type=checkbox] + span:after {
  background-color: transparent;
  top: 23px;
  left: 5px;
  width: 17px;
  height: 10px;
  border-style: solid;
  border-color: #39ecaf;
  border-width: 0 0 4px 4px;
  -o-border-image: none;
  border-image: none;
  -webkit-transform: rotate(-45deg) scale(0);
  -ms-transform: rotate(-45deg) scale(0);
  transform: rotate(-45deg) scale(0);
}
.pure-list__input input[type=checkbox]:checked + span:after {
  content: "";
  transform: rotate(-45deg) scale(1);
  transition: transform 200ms ease-out;
}
.pure-list__input input[type=radio] + span:after, .pure-list__input input[type=radio] + span:before {
  border-radius: 50%;
}
.pure-list__input input[type=radio]:checked + span:before {
  animation: borderscale 300ms ease-in;
}
.pure-list__input input[type=radio]:checked + span:after {
  transform: scale(1);
}
.pure-list__input span {
  display: block;
  position: relative;
  padding: 10px 0 10px 40px;
  vertical-align: middle;
  user-select: none;
  cursor: pointer;
}
.pure-list__input span:before {
  content: "";
  position: absolute;
  top: 17px;
  left: 0;
  width: 31px;
  height: 31px;
  background: #3f6558;
  text-align: center;
  transition: all 0.3s ease;
}
.pure-list__input span:after {
  content: "";
  background-color: #718CC7;
  position: absolute;
  top: 18px;
  left: 6px;
  width: 14px;
  height: 14px;
  transform: scale(0);
  transform-origin: 50%;
  transition: transform 200ms ease-out, -webkit-transform 200ms ease-out;
}

