.game-footer {
  background-color: #0f172a;
  color: #f1f5f9;
  border-top: 1px solid #334155;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 48px 16px;
}

.footer-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 32px;
  margin-bottom: 32px;
}

@media (min-width: 1024px) {
  .footer-grid {
    grid-template-columns: 1fr 2fr 1fr;
  }
}
/* Company Logo Section */
.company-section {
  margin-bottom: 24px;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.logo {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.company-name {
  font-weight: bold;
  font-size: 18px;
  color: white;
  margin: 0;
}

.company-tagline {
  font-size: 14px;
  color: #94a3b8;
  margin: 0;
}

.company-description {
  font-size: 14px;
  color: #cbd5e1;
  line-height: 1.6;
}

/* Company Details Section */
.details-section h4 {
  font-weight: 600;
  color: white;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 16px;
}

.details-card {
  background-color: rgba(30, 41, 59, 0.5);
  padding: 16px;
  border-radius: 8px;
}

.company-title {
  font-weight: 500;
  color: white;
  margin-bottom: 8px;
  font-size: 16px;
}

.license-info {
  display: flex;
  align-items: flex-start;
  gap: 8px;
  margin-bottom: 12px;
}

.license-info:last-child {
  margin-bottom: 0;
}

.license-icon {
  width: 16px;
  height: 16px;
  margin-top: 2px;
  flex-shrink: 0;
}

.license-content p {
  margin: 0;
  color: #cbd5e1;
  font-size: 14px;
  line-height: 1.5;
}

.license-content .license-title {
  font-weight: 500;
  color: white;
  margin-bottom: 4px;
}

/* Contact Section */
.contact-section h4 {
  font-weight: 600;
  color: white;
  margin-bottom: 16px;
  font-size: 16px;
}

.contact-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 16px;
}

.contact-item:last-child {
  margin-bottom: 0;
}

.contact-icon {
  width: 16px;
  height: 16px;
  margin-top: 2px;
  flex-shrink: 0;
}

.contact-content p {
  margin: 0;
  font-size: 14px;
}

.contact-title {
  font-weight: 500;
  color: white;
  margin-bottom: 4px;
}

.contact-text {
  color: #cbd5e1;
  line-height: 1.5;
}

.hotline-link {
  color: #4ade80;
  text-decoration: none;
  transition: color 0.3s ease;
  font-size: 1.8rem;
}

.hotline-link:hover {
  color: #86efac;
}

/* Separator */
.separator {
  height: 1px;
  background-color: #475569;
  margin: 24px 0;
}

/* Bottom Footer */
.bottom-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

@media (min-width: 768px) {
  .bottom-footer {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer-links {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #94a3b8;
}

@media (min-width: 768px) {
  .footer-links {
    flex-direction: row;
    gap: 24px;
  }
}
.footer-nav {
  display: flex;
  align-items: center;
  gap: 16px;
}

.footer-nav a {
  color: #94a3b8;
  text-decoration: none;
  transition: color 0.3s ease;
  display: flex;
  align-items: center;
  gap: 4px;
}

.footer-nav a:hover {
  color: white;
}

.support-button {
  background: transparent;
  border: 1px solid #475569;
  color: #cbd5e1;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.support-button:hover {
  background-color: #1e293b;
}

/* Age Rating Notice */
.age-notice {
  margin-top: 24px;
  padding: 16px;
  background-color: rgba(146, 64, 14, 0.2);
  border: 1px solid rgba(180, 83, 9, 0.3);
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.age-badge {
  width: 32px;
  height: 32px;
  background-color: #d97706;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 14px;
  flex-shrink: 0;
}

.age-content {
  font-size: 14px;
}

.age-title {
  color: #fbbf24;
  font-weight: 500;
  margin-bottom: 4px;
}

.age-text {
  color: #fef3c7;
  line-height: 1.5;
  margin: 0;
}

/* Icons */
.icon {
  width: 16px;
  height: 16px;
  fill: currentColor;
}

.icon-shield {
  color: #3b82f6;
}

.icon-file {
  color: #3b82f6;
}

.icon-calendar {
  color: #10b981;
}

.icon-phone {
  color: #10b981;
}

.icon-map {
  color: #ef4444;
}

.icon-user {
  color: #3b82f6;
}

.logo {
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo img {
  width: 100%;
  height: auto;
  object-fit: contain;
}

footer {
  position: relative;
  z-index: 2;
}

